body {
  --active-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: flex;
}

.container {
  height: 60px;
  position: relative;
}

.list {
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

.item {
  align-items: center;
  height: 100%;
  margin: 0 10px;
  list-style: none;
  display: flex;
}

.link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  text-decoration: none;
}

.item:first-child {
  margin-left: 0;
}

.is-active {
  color: var(--active-color);
  align-items: center;
  height: 100%;
  display: flex;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.item:nth-child(8) {
  margin-right: 0;
}

a:hover {
  color: var(--active-color);
}

